import React from 'react';
import marked from 'marked';
import './App.css';

const EXAMPLE = "# a header (H1 size)\n" +
  "## a sub header (H2 size)\n" +
  "[I'm an inline-style link](https://www.google.com)\n\n" +
  "inline `code`\n\n" + 
  "```javascript\nlet a ='a code block';\n```\n\n" +
  "* A list item\n" +
  "* A list item\n" +
  "* A list item\n\n" +
  "> a blockquote\n\n" +
  '![An image](https://magnusd.com/code/product/logo.svg "Logo Title Text 1")\n\n' +
  "and **bolded** text\n";

class App extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      source: EXAMPLE,
      preview: marked(EXAMPLE)
    }
    this.handleChange = this.handleChange.bind(this);
  }

  handleChange(event) {
    this.setState({
      source: event.target.value,
      preview: marked(event.target.value)
    });
  }

  render() {
    return (
      <div className="App">
      <h1>Markdown Previewer</h1>
      <textarea id="editor" onChange={this.handleChange} value={this.state.source}></textarea>
      <h2>Preview</h2>
      <div id="preview" dangerouslySetInnerHTML={{__html: this.state.preview}}></div>
    </div>
    );
  }
}

export default App;
